import queryString from 'query-string'

import { USER_INFO, BASE_PAGE, ON_LOGIN_CLOSE } from './constant'

export const onDefaultUrl = () => {
  const config = queryString.parseUrl(window.location.href)
  return config.query.redirect
}

export const onTryCatch = async (callback, error) => {
  try {
    if (typeof callback == 'function') {
      await callback()
    }
  } catch (ex) {
    typeof error == 'function' && error()
    console.error(ex)
  }
}

export const onTryCatchPromise = async callback => {
  return new Promise(resolve => {
    onTryCatch(
      async () => {
        const result = await callback()
        resolve(result)
      },
      () => {
        resolve(null)
      }
    )
  })
}

export const setStorage = (key, value) => {
  window.localStorage.setItem(key, value)
}
export const getStorage = key => {
  return window.localStorage.getItem(key)
}

export const removeStorage = key => {
  window.localStorage.removeItem(key)
}

export const clearStorage = () => {
  window.localStorage.clear()
}

export const getStorageJson = key => {
  try {
    return JSON.parse(getStorage(key))
  } catch (error) {
    console.log(error, 'getStorageJson')
    return null
  }
}

export const setStorageJson = (key, value) => {
  setStorage(key, JSON.stringify(value))
}

export const setUserInfo = userInfo => {
  setStorageJson(USER_INFO, userInfo)
}

export const getUserInfo = () => {
  return getStorageJson(USER_INFO)
}

export const removeUserInfo = () => {
  removeStorage(USER_INFO)
}

const objectToQueryStringManual = obj => {
  const parts = []
  for (const [key, value] of Object.entries(obj)) {
    const encodedKey = encodeURIComponent(key)
    const encodedValue = encodeURIComponent(value)
    parts.push(`${encodedKey}=${encodedValue}`)
  }
  return parts.join('&')
}

export const onRedirect = (url, replace = true, params = {}) => {
  const str = objectToQueryStringManual(params)
  const localUrl = str ? `${BASE_PAGE}#/${url}?${str}` : `${BASE_PAGE}#/${url}`
  if (replace) {
    window.location.replace(localUrl)
  } else {
    window.location.href = localUrl
  }
}

export const onExcelModalMessage = config => {
  Office.context.ui.messageParent(JSON.stringify(config))
}

export const onExcelModalClose = () => {
  onExcelModalMessage({ type: ON_LOGIN_CLOSE })
}
