import { onTryCatchPromise, getStorageJson } from '@/utils'
import { PLANT_INFO } from '@/utils/constant'

export class LoginHander {
  onExcelRun(data) {
    return onTryCatchPromise(async () => {
      await Excel.run(async context => {
        const sheet = context.workbook.worksheets.getActiveWorksheet()
        const range = sheet.getRange('A1:CV100')
        range.values = data
        await context.sync()
      })
    })
  }
}

export class CalendarHander {
  onFlatData(data, key) {
    return data.map(v => v[key])
  }
  onExcelRun(data) {
    return onTryCatchPromise(async () => {
      await Excel.run(async context => {
        const sheet = context.workbook.worksheets.getActiveWorksheet()
        const plantInfo = getStorageJson(PLANT_INFO)
        const plantHeader = ['', ...this.onFlatData(data.plant, 'workDate')]
        const plantData = [plantInfo.plantCode, ...this.onFlatData(data.plant, 'isRest')]
        const customerHeader = ['', ...this.onFlatData(data.customer, 'workDate')]
        const customerData = [plantInfo.projectCode, ...this.onFlatData(data.customer, 'isRest')]
        const values = [plantHeader, plantData, customerHeader, customerData]
        const range = sheet.getRangeByIndexes(0, 0, values.length, values[0].length)
        range.values = values
        range.format.autofitColumns()
        range.format.autofitRows()
        await context.sync()
      })
    })
  }
}
