import { BASE_PAGE, ON_LOGIN_CLOSE, ON_LOGIN_SUCCESS, CALENDAR_HANDER } from '@/utils/constant'

import { onTryCatchPromise } from '@/utils'

import { LoginHander, CalendarHander } from '@/commands/hander'

import { get } from 'lodash-es'

export class ExcelModal {
  constructor({ redirect, config, onWatch }) {
    this.redirect = redirect
    this.config = this.onConfig(config)
    this.dialog = null
    this.onWatch = onWatch
    this.onReady()
  }
  onConfig(config) {
    const displayInIframe = get(config, 'displayInIframe', true)
    return {
      height: 70,
      width: 55,
      displayInIframe,
      ...(config || {}),
    }
  }
  onReady() {
    onTryCatchPromise(() => {
      const url = `${BASE_PAGE}#/${this.redirect}`
      Office.context.ui.displayDialogAsync(url, { ...this.config }, config => {
        // Office.AsyncResultStatus.Succeeded windows
        if (config.status === Office.AsyncResultStatus.Succeeded) {
          this.dialog = config.value
          this.onHander(this.dialog)
        } else {
          console.log('on dialog open error', config)
        }
      })
    })
  }
  // 
  onHander(dialog) {
    dialog.addEventHandler(Office.EventType.DialogMessageReceived, async config => {
      const data = this.getMessage(config.message)
      // 
      await this.onLoginHander(data, dialog)
      // 
      await this.onCalendarHander(data, dialog)
      // 
      await this.onModalClose(data, dialog)
    })
  }
  async onLoginHander(data, dialog) {
    if (data.type == ON_LOGIN_SUCCESS) {
      const loginHander = new LoginHander()
      await loginHander.onExcelRun(data.data)
      await this.onSuccess(data, dialog)
      dialog.close()
    }
    return Promise.resolve()
  }
  async onCalendarHander(data, dialog) {
    if (data.type == CALENDAR_HANDER) {
      const hander = new CalendarHander()
      await hander.onExcelRun(data.data)
      await this.onSuccess(data, dialog)
      dialog.close()
    }
    return Promise.resolve()
  }
  async onModalClose(data, dialog) {
    if (data.type == ON_LOGIN_CLOSE) {
      this.onClose(dialog)
    }
    return Promise.resolve()
  }
  async onSuccess(data, dialog) {
    if (data.onSuccess == 'function') {
      await data.onSuccess(dialog)
    }
    return Promise.resolve()
  }
  onClose(dialog) {
    dialog.close()
  }
  getMessage(data) {
    try {
      return JSON.parse(data)
    } catch (error) {
      return {}
    }
  }
}
