import { ExcelModal } from '@/commands/modal'
import { getUserInfo } from '@/utils'

export const onLogin = async event => {
  try {
    // Show login modal
    new ExcelModal({
      redirect: 'login',
    })
  } catch (error) {
    console.error('Error in login handler:', error)
  } finally {
    event.completed()
  }
}

